<template>
<div id="__next">

    <Header />
    <div class="f1e5lflt _2py_lf">

        <div class="_1aU_RW" style="padding-top:2rem">

            <header class="TyPm4j">
                <div class="col-12">
                    <div class="col-2 no-padding style-text-align-left">
                        <button @click="$router.go(-1)" class="style-back-button-back  border-none"><i class="fas fa-angle-left"></i></button>
                    </div>
            
                </div>
            </header>
            <div style="box-shadow: 1px 3px 10px 2px rgba(0, 0, 0, .1);">
                <!-- <div id="map"></div> -->
                <gmap-map :center="center" :zoom="12" style="width:100%;  height: 400px;">
                    <gmap-marker :key="index" v-for="(m, index) in markers" :position="m.position" @click="center=m.position"></gmap-marker>
                </gmap-map>
            </div>

        </div>
    </div>
    <Footer />
</div>
</template>

<script>
import Vue from "vue";
Vue.use(VueGoogleMaps, {
    load: {
        key: 'AIzaSyAL9JearEcPsOe1ZtcQtzN0ujgWcpKc_Ng',
    },
    installComponents: true,

})
import * as VueGoogleMaps from 'vue2-google-maps'
import {
    gmapMap,
    gmapMarker,
    gmapCluster,
    gmapInfoWindow,
    loaded
} from 'vue2-google-maps'
import Header from '@/components/ComponentsPartial/Header'
import Footer from '@/components/ComponentsPartial/Footer'
// import GoogleMap from '@/components/Map/GoogleMap'
export default {

    path: '/maps',
    name: "Maps",
    props: {
        latitude: Number,
        longitude: Number
    },
    components: {
        Header,
        Footer,
        // GoogleMap

    },
    data() {
        return {
            center: {
                lat: 10.758434999999999,
                lng: 106.6670915
            },
            markers: [],
            places: [],
            currentPlace: null
        }
    },
    mounted(){
         this.geolocate();
    },
    created() {
       this.addMarker()
    
    },
    methods: {
        // receives a place object via the autocomplete component
        setPlace(place) {
            this.currentPlace = place;
        },
        addMarker() {

            const marker = {
                lat: parseFloat(this.$route.params.lat),
                lng: parseFloat(this.$route.params.lng)
            };
            console.log('1', marker)
            this.markers.push({
                position: marker
            });
            this.places.push(this.currentPlace);
            this.center = marker;
            this.currentPlace = null;
            console.log('2', this.center)

        },
        geolocate: function () {
            navigator.geolocation.getCurrentPosition(position => {
                this.center = {
                    lat: parseFloat(this.$route.params.lat),
                    lng: parseFloat(this.$route.params.lng)
                };
                console.log('3', this.center)
            });
        }
    },

};
</script>

<style scoped>
#map {
    height: 400px;
    /* The height is 400 pixels */
    width: 100%;
    /* The width is the width of the web page */
}
</style>



